import React, { useEffect, useRef } from "react";
import Post from "./Post";
import ScrollReveal from "scrollreveal";
import styled from "styled-components";
import { useBlogs } from "../../common/hooks/useBlogs";

const HomePostSection = ({ className }) => {
  const listPost = useRef([]);
  const { data } = useBlogs();
  useEffect(() => {
    ScrollReveal().reveal(listPost.current, {
      delay: 300,
      duration: 1000,
      reset: false,
      opacity: 0,
    });
    return () => {};
  }, []);
  return (
    <section className={`${className} home__posts container mt-5`}>
      <p className="text-center fs__title fw-bold">Bài viết mới nhất</p>
      {/* <p className="text-center col-12 col-lg-6 fs__text mx-auto">
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Necessitatibus eos rem eum
        blanditiis, quis reprehenderit!
      </p> */}
      <div className="row mt-5 flex-column flex-lg-row">
        {data?.blogs.map((blog, idx) => {
          return (
            <Post
              key={blog.id_blog}
              ref={(el) => (listPost.current[idx] = el)}
              name={blog.name_b}
              thumbnail={blog.thumbnail}
              created_at={blog.created_at}
              slug={blog.slug}
            />
          );
        })}
      </div>
    </section>
  );
};

export default styled(HomePostSection)``;
