import React, { useState } from "react";
import Collapse from "react-bootstrap/Collapse";
import styled from "styled-components";

const FAGItem = ({ className, question, answer }) => {
  const [isOpen, setIsOpen] = useState(false);
  return (
    <div className={`${className} fag-item mb-2`}>
      <h4 onClick={() => setIsOpen((prev) => !prev)}>{question}</h4>
      <Collapse in={isOpen} appear={false}>
        <div>
          <div className="content">
            <p>{answer}</p>
          </div>
        </div>
      </Collapse>
    </div>
  );
};

export default styled(FAGItem)`
  &.fag-item {
    border-radius: 4px;
    background-color: #d4d4d4;
    h4 {
      padding: 20px;
      margin-bottom: 0;
      font-size: 1rem;
      font-weight: 700;
      position: relative;
      cursor: pointer;
      &::after {
        position: absolute;
        -webkit-font-smoothing: antialiased;
        display: inline-block;
        font-style: normal;
        font-variant: normal;
        text-rendering: auto;
        line-height: 1;
        font-weight: 900;
        font-family: "Font Awesome 5 Free";
        content: "\f107";
        right: 20px;
        transform: rotate(0);
        transform-origin: center;
        transition: all 0.2s ease-in-out;
      }
    }
    .content {
      padding: 0 20px 20px;
    }
  }
`;
