import React, { useEffect, useRef } from "react";
import ScrollReveal from "scrollreveal";
import styled from "styled-components";
import img from "./media/dangkytrainghiem.jpg";

const HeroSection = ({ className }) => {
  const animateRefs = useRef([]);

  useEffect(() => {
    ScrollReveal().reveal(animateRefs.current, {
      duration: 700,
      reset: true,
      interval: 500,
    });
    return () => {};
  }, []);
  return (
    <div className={`${className} py-0 py-lg-5 bg__custom--1`}>
      <section className="container p-3">
        <div className="row align-items-center">
          {/* <div className="home__content col-12 col-lg-4">
            <header
              className="home__content__header fs__title--main"
              ref={(el) => (animateRefs.current[0] = el)}
            >
              Thần số học là gì?
            </header>
            <p
              className="home__content__sub mt-4"
              ref={(el) => (animateRefs.current[1] = el)}
            ></p>
          </div> */}
          <div className="home__video col-12 col-lg-12 p-0">
            <img
              src={img}
              alt=""
              style={{ width: "100%", height: "auto", objectFit: "cover" }}
            />
          </div>
        </div>
      </section>
    </div>
  );
};

export default styled(HeroSection)`
  background-color: ${(props) => props.theme.colorSecondary};
  .home {
    &__content {
      color: #fafafa;
      &__header {
        color: #fafafa;
        font-size: 3em;
        line-height: 60px;
        font-weight: bold;
      }
      &__sub {
        font-size: 1em;
      }
    }
    &__video {
      /* background-color: gray; */
      /* max-height: 400px; */
    }
  }
`;
