import React from "react";
import styled from "styled-components";
import { m } from "framer-motion";

const TestimonialItem = ({ className, trail = 0 }) => {
  // const [
  //   {
  //     avaOpacity,
  //     avaScale,
  //     descOpacity,
  //     descScale,
  //     headerClipPath,
  //     headerOpacity,
  //     headerTransform,
  //     nameOpacity,
  //     nameScale,
  //     textOpacity,
  //     textTransform,
  //   },
  // ] = useSpring(
  //   {
  //     to: [
  //       {
  //         headerClipPath: "inset(0 0 0 0)",
  //         headerOpacity: 1,
  //         headerTransform: "translateY(0)",
  //         textOpacity: 1,
  //         textTransform: "translateY(0)",
  //       },
  //       { avaScale: 1, avaOpacity: 1, nameScale: 1, nameOpacity: 1 },

  //       { descScale: 1, descOpacity: 1 },
  //     ],
  //     from: {
  //       headerClipPath: "inset(100% 0 0 0)",
  //       headerOpacity: 0,
  //       headerTransform: "translateY(10px)",
  //       textOpacity: 0,
  //       textTransform: "translateY(10px)",
  //       avaScale: 0,
  //       avaOpacity: 0,
  //       nameScale: 0,
  //       nameOpacity: 0,
  //       descScale: 0,
  //       descOpacity: 0,
  //     },
  //     config: config.wobbly,
  //   },
  //   []
  // );

  return (
    <div
      className={`${className} d-flex flex-column p-3 p-sm-3 p-lg-4 mx-0 mx-lg-2 my-2`}
    >
      <m.div
        className="testimonial__header"
        // style={{
        //   clipPath: headerClipPath,
        //   opacity: headerOpacity,
        //   transform: headerTransform,
        // }}
        animate={{ clipPath: "inset(0% 0 0 0)", y: 0, opacity: 1 }}
        initial={{ clipPath: "inset(100% 0 0 0)", y: 10, opacity: 0 }}
        transition={{
          delay: trail / 1000 + 0.2,
          type: "spring",
          stiffness: 130,
        }}
      >
        Trải nghiệm của Nguyễn Ngọc Nhẫn
      </m.div>
      <m.div
        className="testimonial__main"
        // style={{ opacity: textOpacity, transform: textTransform }}
        initial={{ opacity: 0, y: 10 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{
          delay: trail / 1000 + 1,
          type: "spring",
          stiffness: 130,
        }}
      >
        <p className="testimonial__content">
          "Tôi rất hài lòng về sản phẩm của Era Vietnam,nó giúp tôi thấu hiểu về
          bản thân mình."
        </p>
      </m.div>
      <div className="d-flex align-content-center testimonial__user">
        <m.img
          src="https://www.sohoceravietnam.com/public/img/meow.jpg"
          alt=""
          className="testimonial__user__img"
          animate={{ scale: 1, opacity: 1 }}
          transition={{
            delay: trail / 1000 + 1,
            type: "spring",
            stiffness: 130,
          }}
          initial={{ scale: 0, opacity: 0 }}
        />
        <div className="testimonial__user_info">
          <m.div
            className="testimonial__user__name fw-bolder"
            animate={{ clipPath: "inset(0% 0 0 0)", y: 0, opacity: 1 }}
            initial={{ clipPath: "inset(100% 0 0 0)", y: 10, opacity: 0 }}
            transition={{
              delay: trail / 1000 + 1.3,
              type: "spring",
              stiffness: 130,
            }}
          >
            Tên
          </m.div>
          <m.div
            className="testimonial__user__desc"
            animate={{ clipPath: "inset(0% 0 0 0)", y: 0, opacity: 1 }}
            initial={{ clipPath: "inset(100% 0 0 0)", y: 10, opacity: 0 }}
            transition={{
              delay: trail / 1000 + 1.6,
              type: "spring",
              stiffness: 130,
            }}
          >
            Senior designer at design studio
          </m.div>
        </div>
      </div>
    </div>
  );
};

export default styled(TestimonialItem)`
  padding: 32px;
  margin: 16px;
  row-gap: 32px;
  background-color: #fafafa;
  box-shadow: 8px 8px 12px 4px rgba(0, 0, 0, 0.2);
  border-radius: 16px;
  position: relative;
  &::before {
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    -webkit-font-smoothing: antialiased;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;
    content: "\f10e";
    position: absolute;
    right: 0;
    top: 0;
    width: 64px;
    height: 64px;
    color: white;
    border-radius: 50%;
    background-color: ${(props) => props.theme.colorSecondary};
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translate(50%, -50%);
  }
  .testimonial {
    &__header {
      font-weight: 900;
      font-size: 32px;
      color: #ccc06b;
    }
    &__content {
      font-weight: 600;
      opacity: 0.6;
    }
    &__user {
      column-gap: 16px;
      &__img {
        border-radius: 50%;
        height: 50px;
        width: 50px;
        object-fit: cover;
      }
      &__desc {
        opacity: 0.4;
      }
    }
  }
`;
