import React, { forwardRef } from "react";
import TimeAgo from "timeago-react";
import styled from "styled-components";
import { Link } from "react-router-dom";

const Post = ({ className, name, thumbnail, created_at, slug }, ref) => {
  return (
    <div className={`${className} col-12 col-lg-3`} ref={ref}>
      <div className="home__posts__card">
        <Link to={`/blog/${slug}`}>
          <div className="home__posts__img">
            <img src={thumbnail} alt="Tin tức Era" />
          </div>
          <div className="py-1">
            <p className="fs__text fw-bold text-center">{name}</p>
            <p className="fs__details text-end">
              <TimeAgo datetime={created_at} locale="vi" />
            </p>
          </div>
        </Link>
      </div>
    </div>
  );
};

export default styled(forwardRef(Post))`
  flex: 1;
  .card-container {
    background-color: #383838;
    border-radius: 16px;
  }
  .home__posts {
    &__card {
      height: 100%;
      padding: 8px;
      border-radius: 16px;
      /* box-shadow: 2px 4px rgba(0, 0, 0, 14%); */
      position: relative;
      &:hover {
        /* &::before {
          clip-path: inset(0 0 0 0 round 16px);
        } */
        .home__posts__img img {
          box-shadow: 1px 2px 4px 4px rgba(0, 0, 0, 20%);
        }
      }
      /* &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        margin: -0.4rem;
        background-color: #d4d4d4;
        border-radius: 16px;
        z-index: -1;
        transform-origin: 100% 50%;
        transition: clip-path 0.3s ease 0.1s;
        clip-path: inset(2rem 0 2rem 2rem round 16px);
      } */
      a {
        color: black;
        &:hover {
          color: ${(props) => props.theme.colorTextTitle};
        }
      }
    }
    &__img {
      > img {
        border-radius: 16px;
        height: 200px;
        width: 100%;
        /* height: auto; */
        object-fit: cover;
        transition: box-shadow 0.3s ease;
        box-shadow: 2px 4px 8px 8px rgba(0, 0, 0, 14%);
      }
    }
  }
`;
